<template>
  <div class="base-box">
    <vue-seamless-scroll :data='navList'
      :class-option="optionHover">
      <div class="navList">
        <div v-for="(it,i) in navList"
          :key="i"
          class="navList-item">
          <lottie :options="{animationData: it.json}"
            :height="100"
            :width="100"
            v-on:animCreated="handleAnimation" />
        </div>
      </div>
    </vue-seamless-scroll>
  </div>

</template>

<script>
import Lottie from 'vue-lottie'
// import animationData from "@a/js/icon-use-json/sj-sjcj.json"
const requireComponent = require.context('@a/js/icon-use-json/', true, /\w+\.(json)$/)
const icons = []
requireComponent.keys().map(fileName => {
  icons.push({
    name: fileName.replace('./', '').replace('.json', ''),
    json: requireComponent(fileName)
  })
  // icons[fileName.replace('./', '').replace('.json', '')] = requireComponent(fileName)
})
export default {
  components: {
    Lottie

  },
  data () {
    return {
      navList: icons,
      defaultOptions: { animationData: icons.bi },
      animationSpeed: 1
    }
  },
  computed: {
    optionHover () {
      return {

        // step: 100, // 数值越大速度滚动越快
        // limitMoveNum: 2, // 开始无缝滚动的数据量 this.dataList.length
        hoverStop: true, // 是否开启鼠标悬停stop
        direction: 2, // 0向下 1向上 2向左 3向右
        // openWatch: true, // 开启数据实时监控刷新dom
        // singleHeight: 26, // 单步运动停止的高度(默认值0是无缝不停止的滚动) direction => 0/1
        singleWidth: 160, // 单步运动停止的宽度(默认值0是无缝不停止的滚动) direction => 2/3
        waitTime: 3000 // 单步运动停止的时间(默认值1000ms)
      }
    }
  },
  created () {
    // this.setList()
  },
  methods: {
    setList () {

    },

    handleAnimation: function (anim) {
      this.anim = anim
    },

    stop: function () {
      this.anim.stop()
    },

    play: function () {
      this.anim.play()
    },

    pause: function () {
      this.anim.pause()
    },

    onSpeedChange: function () {
      this.anim.setSpeed(this.animationSpeed)
    }

  }
}
</script>
