import Vue from 'vue'
import VueRouter from 'vue-router'
import index from '@v/index'
import Home from '@v/home'
import Demo from '@v/demo'
import Blogs from '@v/blogs'
import Menu2 from '@v/menu2'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'index',
    redirect: '/home',
    component: index,
    children: [
      {
        path: '/home',
        name: 'Home',
        component: Home
      },
      {
        path: '/demo',
        name: 'Demo',
        component: Demo
      },
      {
        path: '/blogs',
        name: 'Blogs',
        component: Blogs
      },
      {
        path: '/menu2',
        name: 'Menu2',
        component: Menu2
      }
    ]

  }
]

const router = new VueRouter({
  mode: 'hash',
  base: process.env.BASE_URL,
  routes
})

export default router
